/* üé® Modal container with smooth background */
.modal-actualizar-cliente {
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
}
  
/* üìå Dialog styles */
.modal-actualizar-cliente .MuiDialog-paper {
    border-radius: 12px;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}
  
/* üîñ Title styling */
.modal-actualizar-cliente .MuiDialogTitle-root {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: bold;
    color: #d32f2f; /* üî¥ Color rojo */
    text-transform: uppercase;
}

.modal-actualizar-cliente .modal-icon {
    font-size: 28px;
    color: #d32f2f; /* üî¥ Mismo color que el t√≠tulo */
}
  
/* üìù Input fields */
.modal-actualizar-cliente .input-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 12px;
}

.modal-actualizar-cliente .input-field {
    border-radius: 8px;
    border: 1px solid #ccc;
    padding: 10px;
    width: 100%;
    transition: all 0.3s ease;
}

.modal-actualizar-cliente .input-field:focus {
    border-color: #d32f2f;
    box-shadow: 0px 0px 8px rgba(211, 47, 47, 0.4);
}
  
/* üé≠ Buttons */
.modal-actualizar-cliente .MuiDialogActions-root {
    padding: 15px;
    justify-content: space-between;
}

.modal-actualizar-cliente .cancel-button {
    color: #555;
    font-weight: bold;
    transition: all 0.3s ease-in-out;
}

.modal-actualizar-cliente .cancel-button:hover {
    color: #000;
}

.modal-actualizar-cliente .update-button {
    background: #d32f2f;
    color: white;
    border-radius: 8px;
    font-weight: bold;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

.modal-actualizar-cliente .update-button:hover {
    background: #b71c1c;
    transform: translateY(-2px);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
}
  
/* üì± Responsive adjustments */
@media (max-width: 600px) {
    .modal-actualizar-cliente .MuiDialog-paper {
        width: 90%;
    }

    .modal-actualizar-cliente .MuiDialogActions-root {
        flex-direction: column;
        gap: 10px;
    }
}
