/* üåÖ Manteniendo el fondo original */
.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to bottom, #F8E9E9, #FFFFFF);
  overflow: hidden;
}

/* üìå Formulario con Glassmorphism */
.login-box {
  background: rgba(255, 255, 255, 0.25); /* M√°s opacidad para mejorar la visibilidad */
  backdrop-filter: blur(12px);
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 90%;
  max-width: 400px;
  animation: fadeIn 0.8s ease-in-out;
}

/* üéÄ T√≠tulo */
.title {
  font-size: 26px;
  font-weight: bold;
  color: #FF2A2A;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

/* üåä Inputs con contenido centrado */
.input {
  display: flex; /* Permite alinear el contenido */
  justify-content: center; /* Centra el contenido horizontalmente */
  align-items: center; /* Centra el contenido verticalmente */
  margin-top: 12px;
  padding: 12px;
  border: 2px solid rgba(255, 42, 42, 0.3);
  border-radius: 8px;
  font-size: 16px;
  width: 100%;
  text-align: center; /* üî• Centra el texto dentro del input */
  color: #FF2A2A;
  background: rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease-in-out;
  outline: none;
}

/* Brillo al hacer focus */
.input:focus {
  border-color: #FF2A2A;
  box-shadow: 0px 0px 10px rgba(255, 42, 42, 0.5);
}

/* üéØ Placeholder estilizado */
.input::placeholder {
  text-align: center; /* üî• Asegura que el placeholder tambi√©n est√© centrado */
  color: rgba(255, 42, 42, 0.7);
  font-style: italic;
}

/* üìå Botones con hover moderno */
.button-box {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 20px;
}

.button-login,
.button-register {
  background: #FF2A2A;
  color: white;
  font-size: 16px;
  font-weight: bold;
  padding: 12px;
  border-radius: 30px;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
}

/* Hover con efecto */
.button-login:hover,
.button-register:hover {
  transform: scale(1.05);
  background: #FF5050;
}

/* Efecto de presionado */
.button-login:active,
.button-register:active {
  transform: scale(0.95);
}

/* Bot√≥n de Registrarse con borde */
.button-register {
  background: transparent;
  border: 2px solid #FF2A2A;
  color: #FF2A2A;
}

.button-register:hover {
  background: #FF2A2A;
  color: white;
}
