/* üé® Modal container with smooth background */
.modal-agregar-producto {
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* üìå Dialog styles */
  .modal-agregar-producto .MuiDialog-paper {
    border-radius: 12px;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  }
  
  /* üîñ Title styling */
  .modal-agregar-producto .MuiDialogTitle-root {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: bold;
    color: #f44336;
    text-transform: uppercase;
  }
  
  /* üîç Search bar styling */
  .modal-agregar-producto .busqueda-producto {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .modal-agregar-producto .MuiInputBase-root {
    border-radius: 8px;
    border: 1px solid #ccc;
    transition: all 0.3s ease;
  }
  
  .modal-agregar-producto .MuiInputBase-root:focus-within {
    border-color: #f44336;
    box-shadow: 0px 0px 8px rgba(244, 67, 54, 0.4);
  }
  
  /* üîò Search button */
  .modal-agregar-producto .MuiIconButton-root {
    background: #f44336;
    color: white;
    transition: all 0.3s ease-in-out;
  }
  
  .modal-agregar-producto .MuiIconButton-root:hover {
    background: #d32f2f;
  }
  
  /* üè∑Ô∏è Product details */
  .modal-agregar-producto .detalle-producto {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 15px;
  }
  
  .modal-agregar-producto .detalle-producto .MuiChip-root {
    font-size: 14px;
    padding: 8px;
    border-radius: 6px;
  }
  
  /* üìë Subtotal section */
  .modal-agregar-producto .subtotal {
    font-size: 18px;
    font-weight: bold;
    color: #d32f2f;
    text-align: right;
    margin-top: 10px;
  }
  
  /* üé≠ Buttons */
  .modal-agregar-producto .MuiDialogActions-root {
    padding: 15px;
    justify-content: space-between;
  }
  
  .modal-agregar-producto .MuiButton-contained {
    background: #ff2a2a;
    color: white;
    border-radius: 8px;
    font-weight: bold;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  }
  
  .modal-agregar-producto .MuiButton-contained:hover {
    background: #d32f2f;
    transform: translateY(-2px);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
  }
  
  .modal-agregar-producto .MuiButton-text {
    color: #555;
    font-weight: bold;
  }
  
  .modal-agregar-producto .MuiButton-text:hover {
    color: #000;
  }
  
  /* üì± Responsive adjustments */
  @media (max-width: 600px) {
    .modal-agregar-producto .MuiDialog-paper {
      width: 90%;
    }
  
    .modal-agregar-producto .subtotal {
      text-align: center;
    }
  
    .modal-agregar-producto .MuiDialogActions-root {
      flex-direction: column;
      gap: 10px;
    }
  }
  