/* üåÖ Fondo con degradado */
.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #f8e9e9, #ffffff);
  position: relative;
}

/* üéÄ T√≠tulo */
.title {
  font-size: 32px;
  font-weight: bold;
  color: #ff2a2a;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

/* üìå Contenedor con efecto Glassmorphism */
.menu-box {
  padding: 50px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  width: 90%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

/* üìå Grid para organizar los botones en una matriz 2x2 */
.menu-grid {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* üìå Ajustar tama√±o uniforme de los botones */
.grid-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* üéØ Botones grandes y del mismo tama√±o */
.menu-button {
  width: 200px;
  height: 120px;
  background: #ff2a2a !important;
  color: white !important;
  font-size: 18px !important;
  font-weight: bold !important;
  text-transform: uppercase !important;
  border-radius: 16px !important;
  transition: all 0.3s ease-in-out !important;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2) !important;
}

/* Hover de los botones */
.menu-button:hover {
  transform: scale(1.05);
  background: #ff5050 !important;
}

/* Bot√≥n de Cerrar Sesi√≥n con borde */
.menu-button.outlined {
  background: transparent !important;
  border: 3px solid #ff2a2a !important;
  color: #ff2a2a !important;
}

.menu-button.outlined:hover {
  background: #ff2a2a !important;
  color: white !important;
}

/* üìå Mensaje de bienvenida con fuente redondeada y sin sombra */
.welcome-message {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 28px;
  font-weight: bold;
  color: #e60012; /* Rojo vibrante inspirado en Miniso */
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-family: "Poppins", sans-serif; /* Fuente redondeada y moderna */
}

