/* Box principal */
.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* Asegura que el contenido est√© bien distribuido */
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom, #F8E9E9, #FFFFFF);
}

/* Contenedor del logo */
.logo-box {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    /* Espaciado inferior para separar del bot√≥n */
}

/* Imagen del logo */
.logo-box img {
    width: 200px;
    /* Ajuste din√°mico para evitar problemas en pantallas peque√±as */
    height: auto;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

/* Contenedor del bot√≥n */
.button-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

/* Bot√≥n estilo Miniso */
.button-box button {
    background-color: #FFFFFF;
    color: #FF2A2A;
    padding: 14px 28px;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    border: 2px solid #FF2A2A;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 400px;
    /* Para evitar que sea demasiado grande */
    width: 90%;
    /* Para que se adapte bien en pantallas peque√±as */
    text-align: center;
}

/* Efectos hover y active */
.button-box button:hover {
    background-color: #FF2A2A;
    color: #FFFFFF;
    transform: scale(1.05);
}

.button-box button:active {
    transform: scale(0.95);
}