/* üåÖ Main container with background */
.main-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #f8e9e9, #ffffff);
  padding: 20px;
  overflow-x: hidden;
}

/* üéÄ Title styling */
.factura-title {
  font-size: 32px;
  font-weight: bold;
  color: #f44336;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
  text-align: center;
}

/* üìå Main content container with glassmorphism effect */
.factura-box {
  width: 90%;
  max-width: 1200px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  overflow: visible;
}

/* Button styling */
.back-button,
.add-product-button,
.search-button,
.update-button,
.facturar-button {
  background: #ff2a2a !important;
  color: white !important;
  width: 200px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-radius: 8px !important;
  transition: all 0.3s ease-in-out !important;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15) !important;
}

.back-button:hover,
.add-product-button:hover,
.search-button:hover,
.update-button:hover,
.facturar-button:hover {
  transform: translateY(-2px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2) !important;
}

/* Search and client info boxes */
.search-box,
.cliente-info {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.search-box:hover,
.cliente-info:hover {
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.15);
}

.search-input {
  margin-top: 10px;
}

/* Table container */
.table-container {
  margin-top: 10px;
  border-radius: 10px;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  max-height: 45vh;
  position: relative;
}

/* Table header cells */
.header-cell {
  font-weight: bold;
  background-color: red !important;
  color: white !important;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 0.5px;
  position: sticky;
  top: 0;
  z-index: 10;
}

/* Table row hover effect */
.table-row:hover {
  background-color: #ffebee !important;
}

/* Mobile product cards */
.product-card {
  border-left: 4px solid #f44336;
  transition: all 0.3s ease;
}

.product-card:hover {
  transform: translateY(-2px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15) !important;
}

/* Total container */
.total-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 15px;
  background-color: rgba(245, 245, 245, 0.9);
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.total-container:hover {
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.15);
}

/* Delete button */
.delete-button {
  color: #f44336 !important;
  transition: all 0.2s ease;
}

.delete-button:hover {
  background-color: rgba(244, 67, 54, 0.1) !important;
  transform: scale(1.1);
}

/* Product section container */
.products-section {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .factura-title {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .factura-box {
    width: 95%;
    padding: 10px;
  }

  .total-container {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  .back-button,
  .add-product-button,
  .search-button,
  .update-button,
  .facturar-button {
    width: 100%;
    margin-bottom: 8px;
  }

  .table-container {
    max-height: 40vh;
  }

  /* Smaller padding for mobile */
  .search-box,
  .cliente-info {
    padding: 15px !important;
  }
}

@media (min-width: 601px) and (max-width: 960px) {
  .factura-title {
    font-size: 28px;
  }

  .table-container {
    max-height: 40vh;
  }

  /* Adjust button spacing for tablets */
  .back-button,
  .add-product-button {
    margin-bottom: 0;
  }
}

/* Fix for scrolling issues */
@media (max-height: 700px) {
  .main-container {
    height: auto;
    min-height: 100vh;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .table-container {
    max-height: 35vh;
  }
}