/* üåÖ Main container with background */
.resumen-factura-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #f8e9e9, #ffffff);
  padding: 20px;
  overflow-x: hidden;
}

/* üéÄ Title styling */
.resumen-factura-titulo {
  font-size: 32px;
  font-weight: bold;
  color: #f44336;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
  text-align: center;
}

/* üìå Main content container with glassmorphism effect */
.resumen-factura-card {
  width: 90%;
  max-width: 800px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  overflow: visible;
}

/* üìë Cliente info box */
.resumen-factura-cliente {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  padding: 15px;
  margin-bottom: 15px;
}

.resumen-factura-cliente:hover {
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.15);
}

/* üìÖ Fecha de emisi√≥n */
.resumen-factura-fecha {
  font-size: 14px;
  font-weight: bold;
  color: #555;
  margin-bottom: 15px;
}

/* üìä Table container - Reduced height */
.resumen-factura-tabla {
  margin-top: 10px;
  border-radius: 10px;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  max-height: 30vh;
  /* Reduced from 45vh to 30vh */
  position: relative;
  margin-bottom: 20px;
}

/* Compact table cells */
.resumen-factura-tabla .MuiTableCell-root {
  padding: 8px 16px;
  /* Reduced padding */
}

/* Table header cells - matching the red header from original */
.resumen-factura-tabla .MuiTableHead-root .MuiTableCell-root {
  font-weight: bold;
  background-color: red !important;
  color: white !important;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 0.5px;
  position: sticky;
  top: 0;
  z-index: 10;
}

/* Table row hover effect */
.resumen-factura-tabla .MuiTableBody-root .MuiTableRow-root:hover {
  background-color: #ffebee !important;
}

/* üí∞ Total container */
.resumen-factura-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 15px;
  background-color: rgba(245, 245, 245, 0.9);
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.resumen-factura-total:hover {
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.15);
}

/* üîò Button container */
.resumen-factura-botones {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 16px;
}

/* üîò Button styling */
.resumen-factura-botones .MuiButton-containedPrimary {
  background: #ff2a2a !important;
  color: white !important;
  width: 200px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-radius: 8px !important;
  transition: all 0.3s ease-in-out !important;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15) !important;
}

.resumen-factura-botones .MuiButton-containedPrimary:hover {
  transform: translateY(-2px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2) !important;
}

.resumen-factura-botones .MuiButton-outlinedSecondary {
  width: 200px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-radius: 8px !important;
  transition: all 0.3s ease-in-out !important;
}

.resumen-factura-botones .MuiButton-outlinedSecondary:hover {
  transform: translateY(-2px);
}

/* üì± Responsive adjustments */
@media (max-width: 600px) {
  .resumen-factura-titulo {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .resumen-factura-card {
    width: 95%;
    padding: 10px;
  }

  .resumen-factura-total {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  /* Button container - vertical alignment for mobile */
  .resumen-factura-botones {
    flex-direction: column !important;
    align-items: center;
    gap: 15px !important;
    width: 100%;
  }

  .resumen-factura-botones .MuiButton-containedPrimary,
  .resumen-factura-botones .MuiButton-outlinedSecondary {
    width: 100%;
    max-width: 280px;
  }

  .resumen-factura-tabla {
    max-height: 25vh;
    /* Reduced from 40vh to 25vh for mobile */
  }
}

@media (min-width: 601px) and (max-width: 960px) {
  .resumen-factura-titulo {
    font-size: 28px;
  }

  .resumen-factura-tabla {
    max-height: 28vh;
    /* Reduced from 40vh to 28vh for tablets */
  }
}

/* Fix for scrolling issues */
@media (max-height: 700px) {
  .resumen-factura-container {
    height: auto;
    min-height: 100vh;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .resumen-factura-tabla {
    max-height: 22vh;
    /* Further reduced for small height screens */
  }
}
