/* üìå Contenedor principal */
.main-container {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* üéÄ T√≠tulo */
.factura-title {
    font-size: 32px;
    font-weight: bold;
    color: #ff2a2a;
    text-transform: uppercase;
    margin-bottom: 10px;
    text-align: center;
}

/* üì¶ Caja de b√∫squeda */
.factura-box {
    padding: 20px;
    max-width: 1100px;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    width: 90%;
}

/* üîé Dise√±o de b√∫squeda adaptable */
.search-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    margin-bottom: 15px;
}

/* üî¥ Bot√≥n de regreso y b√∫squeda (Mismo tama√±o y estilo) */
.back-button,
.search-button {
    width: 200px;
    height: 50px;
    background: #ff2a2a !important;
    color: white !important;
    font-size: 16px !important;
    font-weight: bold !important;
    text-transform: uppercase !important;
    border-radius: 12px !important;
    transition: all 0.3s ease-in-out !important;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2) !important;
}

.back-button:hover,
.search-button:hover {
    background-color: #e60000 !important;
    /* Rojo m√°s oscuro al pasar el mouse */
    transform: scale(1.07);
}

/* üîé Input de b√∫squeda */
.search-input {
    flex-grow: 1;
    padding: 12px;
    font-size: 16px;
    border: 2px solid #ff2a2a;
    border-radius: 8px;
    outline: none;
}

/* üìå Tabla */
.table-container {
    max-height: 300px;
    overflow-y: auto;
    border-radius: 8px;
    width: 100%;
}

/* üü• Estilos de la tabla */
.header-cell {
    background-color: red !important;
    color: white !important;
    font-weight: bold;
}

.table-row:hover {
    background-color: #ffe0e0;
}

/* üí∞ Estilos del Total */
.total-row {
    background-color: #ffffff;
    border-top: 3px solid #fff0f0;
}

.total-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    border-radius: 5px;
    background: linear-gradient(to right, #ffffff, #fee7e1);
    border-left: 5px solid #ff0303;
}

.total-label {
    font-size: 18px;
    font-weight: bold;
    color: #141414;
    text-transform: uppercase;
}

.total-amount {
    font-size: 22px;
    font-weight: bold;
    color: #000000;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
}

/* üì± Responsividad */
@media (max-width: 768px) {
    .search-container {
        flex-direction: column;
        align-items: stretch;
    }

    .search-button,
    .back-button {
        width: 100%;
    }

    .total-container {
        flex-direction: column;
        text-align: center;
        padding: 10px;
    }

    .total-label {
        font-size: 16px;
    }

    .total-amount {
        font-size: 20px;
    }
}